import { Component, ViewChild } from '@angular/core';
import { Platform, App, Nav, ModalController, MenuController, NavController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { GlobalVarsProvider } from '../providers/global-vars/global-vars';
import { Device } from '@ionic-native/device';
import { HomePage } from '../pages/home/home';
import { Storage } from '@ionic/storage';
import { Sim } from "@ionic-native/sim";



@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  rootPage: any = 'LoginPage';
  // rootPage:any = 'BalancePage';
  //public simInfo: any;
  langs: any;
  simCards: any;
  //sim: any;
  constructor(private storage: Storage, platform: Platform, app: App, statusBar: StatusBar,
    public globalVars: GlobalVarsProvider, splashScreen: SplashScreen, device: Device, public sim: Sim) {
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
      globalVars.uiid = device.uuid;
      //   globalVars.simnumber =  sim.getSimInfo();
      this.globalVars.seekPermissions().then(data => {
        if (data === true) {
          this.globalVars.getSimInfo();
        }else{
          console.log("PHONE_PERMISSION_DENIED");
          this.globalVars.getSimInfo();
        }
      });

    });
    this.langs = this.globalVars.doTranslate(this.globalVars.getLang());
    this.globalVars.showTimeout();

    //  this.storage.clear();

  }


  getSimCards() {
    return this.simCards
  }

  openPage(page) {
    this.nav.push(page);
  }



  go(page) {
    this.nav.setRoot(page);
  }




  ionDidOpen() {
    this.langs = this.globalVars.doTranslate(this.globalVars.getLang());
    console.log("DOne");
  }

}



