    cleanDigit(numb) {
        var ni = numb.match(/^[0-9]+$/);
        ni = parseInt(ni);
        return ni.toString();
    }

    checkValidEmail(email) {
        var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(email)) {
            return true;
        } else {
            return false;
        }
    }


    getIMSIdata() {
        return this.simCards
    }

    getSimInfo(): void {
        this.sim
            .requestReadPermission()
            .then(
                () => console.log("Permission granted"),
                () => console.log("Permission denied")
            );

        this.sim
            .getSimInfo()
            .then(
                info => (this.simCards = info, console.log("SIM_INFO_DEBUG", info)),
                err => console.log("SIM_INFO_DEBUG_ERROR: ", err)
            );
    }

    seekPermissions(): Promise<any> {
        return new Promise(resolve => {
            let permissions = cordova.plugins.permissions;
            permissions.requestPermission(
                permissions.READ_PHONE_STATE,
                successCallback,
                errorCallback
            );

            function errorCallback() {
                resolve(false);
            }

            function successCallback(status) {
                if (!status.hasPermission) errorCallback();
                if (status.hasPermission) resolve(true);
            }
        });
    }


    trimPhome(phon) {
        phon = phon.replace(/\s/g, "");
        var lastnine = phon.substr(phon.length - 9);
        return '255' + lastnine;
    }
