import { BrowserModule } from '@angular/platform-browser';
import { HttpModule, Http } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { Device } from '@ionic-native/device';
import { Sim } from '@ionic-native/sim';
//import { Sim, SimOriginal } from '@ionic-native/sim';


import { AndroidPermissions } from '@ionic-native/android-permissions';
import { Network } from '@ionic-native/network';
import { Contacts } from '@ionic-native/contacts';
import { SocialSharing } from '@ionic-native/social-sharing';
import { Toast } from '@ionic-native/toast';


//import {Uid} from '@ionic-native/uid';



import { IonicStorageModule } from '@ionic/storage';
// import { HTTP, HTTPResponse } from '@ionic-native/http';
import { NgIdleKeepaliveModule } from '@ng-idle/keepalive';
//import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { CallNumber } from '@ionic-native/call-number';
// import { DatePicker } from '@ionic-native/date-picker';
// import { LaunchNavigator } from '@ionic-native/launch-navigator/ngx';

//providers
import { MyApp } from './app.component';
import { AlertServiceProvider } from '../providers/alert-service/alert-service';
import { ClientdataProvider } from '../providers/clientdata/clientdata';
import { GlobalVarsProvider } from '../providers/global-vars/global-vars';
import { ApiConnectProvider } from '../providers/api-connect/api-connect';
import { ValidatorProvider } from '../providers/validator/validator';
import { ContactsProvider } from '../providers/contacts/contacts';
import { ConnectivityServiceProvider } from '../providers/connectivity-service/connectivity-service';

@NgModule( {
  declarations: [
    MyApp
  ],
  imports: [
    HttpClientModule,
    BrowserModule,
    HttpModule,
    IonicModule.forRoot( MyApp ),
    NgIdleKeepaliveModule.forRoot(),
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [ IonicApp ],
  entryComponents: [  
    MyApp
  ],
  providers: [
    AndroidPermissions, CallNumber,
    StatusBar, Device, Contacts,
    SplashScreen, Network,
    SocialSharing,
    Toast, Sim,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    ConnectivityServiceProvider,
    AlertServiceProvider,
    ClientdataProvider,
    GlobalVarsProvider,
    ApiConnectProvider,
    ValidatorProvider,
    ContactsProvider
  ]
} )
export class AppModule { }



